.mode-choice {
    width: 1149px;
    height: 703px;

    &__title {
        font-weight: 600;
        font-size: 32px;
        margin-top: 40px;
    }
    .button {
        margin-top: 60px;
    }
}

.cardsets {
    display: flex;
    align-items: center;
    justify-content: center;
    &__cardset {
        border-radius: 20px;
        width: 179px;
        height: 117px;
        box-shadow: $box-shadow;
        display: flex;
        align-items: center;
        padding: 5px 7px;
        margin: 35px 17px;
        cursor: pointer;
    }
    &__cardset:hover {
        box-shadow: $box-shadow-hover;
    }
    &__cardset:active {
        box-shadow: $box-shadow-delete;
    }
    &__add-cardset {
        width: 179px;
        height: 117px;
        margin: 35px 17px;
        transition: 0.5s ease-in-out;
        cursor: pointer;
    }
    &__add-cardset:hover {
        transform: scale(1.1);
    }
}